<template>
  <view class="input-container">
    <view class="left-box" @click="handleClick">
      <uv-input
        shape="circle"
        prefixIcon="search"
        prefixIconStyle="font-size: 22px;color: #909399"
        :customStyle="{
          backgroundColor: '#F7F9FC',
          height: '72rpx',
          boxSizing: 'border-box',
          border: 'none',
        }"
        v-model="content"
        :placeholder="placeholder"
        border="surround"
        clearable
        @clear="handleClear"
        @confirm="handleConfirm"
        :disabled="disabled"
      ></uv-input>
    </view>
    <view v-if="show" class="right-box" @click.stop="handleMap">
      <image src="@/static/images/mp-icon.png" class="img" mode=""></image>
    </view>
  </view>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    disabled: {
      type: Boolean,
      default: true,
    },
    placeholder: {
      type: String,
      default: '搜索内容',
    },
  },
  data() {
    return {
      content: '',
    }
  },
  methods: {
    handleClick() {
      this.$emit('search', this.content)
    },
    handleClear() {
      this.content = ''
      this.$emit('clear', this.content)
    },
    handleConfirm() {
      this.$emit('confirm', this.content)
    },
    handleMap() {
      this.$emit('map')
    },
  },
}
</script>

<style lang="scss" scoped>
.input-container {
  width: 100%;
  padding: 0 32rpx;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  .left-box {
    flex: 1;
  }
  .right-box {
    flex-shrink: 0;
    margin-left: 32rpx;
  }
  .img {
    display: block;
    width: 44rpx;
    height: 44rpx;
  }
}
</style>
